<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal wallet-withdrawal-modal" @click.stop="">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            <div class="modal-content">
        <p class="window-title">{{$localizeFilter('withdrawal')}}</p>

        <p class="window-text">{{$localizeFilter('wallet_withdrawal_modal_text_amount', [coinTicker])}}</p>
        <div class="input-field">
            <input id="amount" class="input-class" :placeholder="$localizeFilter('amount_in', [coinTicker])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                {{ error.$message }}
            </small>
        </div>

        <WalletOptions class="walletOptions" :options="networksForCoin" @selectOption="selectNetwork"/>

        <!-- <Toggle style="margin-top: 10px;" :selected="selectedMethod" :title="$localizeFilter('withdrawal_method')+':'" :variants="methods" @toggle="toggle" /> -->

        <p v-if="this.getNetworkByTicker(this.network)" class="window-text">{{$localizeFilter('wallet_withdrawal_modal_text_wallet')}}</p>
          <div class="input-field wallet-field">
            <input v-if="this.getNetworkByTicker(this.network)" id="walletAddress" class="input-class wallet-input" :placeholder="$localizeFilter('wallet_number', [$filters.capitalizeFirstLetter(this.getNetworkByTicker(this.network).networkFullName)])" type="text" v-model.trim="walletAddress"  :class="{ invalid: v$.walletAddress.$error }"/>
            <small class="error" v-for="(error, index) of v$.walletAddress.$errors" :key="index">
                {{ error.$message }}
            </small>
        </div>

    </div>
    <button v-if="this.getNetworkByTicker(this.network)" class="basic-button submit-button" :class="submitInProgress ? 'inactive' : ''" @click="submitHandler">{{$localizeFilter('next')}}</button>

    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'
import WalletOptions from '../WalletOptions.vue';

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    decimalsNumber = BigInt(decimalsNumber)
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        coinTicker: {
            type: String,
            required: true
        }
    },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
            },
            walletAddress: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                minLength: helpers.withMessage(localizeFilter('min_length', [this.minLength]), minLength(this.minLength)),
                maxLength: helpers.withMessage(localizeFilter('max_length', [this.maxLength]), maxLength(this.maxLength)),
            }
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        amount: "",
        minAmount: 0,
        walletAddress: "",
        walletType: "Bitcoin",
        submitInProgress: false,
    }),
    methods: {
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }

            var WAValidator = require('wallet-address-validator');
            if(!WAValidator.validate(this.walletAddress, this.validatorParam)){
                this.$error({message: localizeFilter('incorrect_wallet_address')})
                return
            }

            this.submitInProgress = true

            // send data to store
            try {
                let amount = multiplyNumberByDecimals(this.amount, this.assetDecimalsMap[this.coinTicker]).toString();
                const data = {network: this.network, amount, to: this.walletAddress, currencyCode: this.coinTicker}
                let {withdrawId, commission} = await this.$store.dispatch('requestWithdraw', data)
                this.$emit('showConfirmationModal', {withdrawId, commission})
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } catch(e) {
                this.$error(e)
                // console.log(e)
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 
        },
        async selectNetwork(network) {
            await this.$store.dispatch('setWithdrawFromWalletNetwork',{network})
            // this.$emit('networkSelected', network)
        },

        getNetworkByTicker(network) {
            if (this.network === null) {return null}
            return this.$store.getters.networkByTicker(network)
        }
    },
    computed: {
        assetDecimalsMap() {
            return this.$store.getters.assetDecimalsMap;
        },
        networksForCoin() {
            return this.$store.getters.networksForCoin(this.coinTicker)
        },
        network() {
            return this.$store.getters.withdrawFromWalletNetwork
        },
        validatorParam() {
            if (this.network === null) {return null}
            return this.getNetworkByTicker(this.network).validatorParam
        },
        minLength() {
            if (this.network === null) {return 0}
            return this.getNetworkByTicker(this.network).minLength
        },
        maxLength() {
            if (this.network === null) {return 100}
            return this.getNetworkByTicker(this.network).maxLength
        }
    },
    components: { WalletOptions }
})
</script>


