<template>
    <div class="coin-wallet">
      <button class="back-button" @click="$router.back()">
      <img src="@/assets/newImages/sidebar/double-arrows.svg" class="double-arrow">
      <!-- <img class="back-button-arrow" src="@/assets/newImages/black_arrow.png"/> -->
      <p class="back-button-text">{{`${$localizeFilter('wallet')} ${coinTicker} ${isDemo ? $localizeFilter('demo') : ''}`}}</p>
      <!-- <img :src="require('@/assets/newImages/coins/icons/' + coinTicker.toLowerCase() + '_icon.png')" class="coin-icon"> -->
      </button>

      <div class="wallet-hint">{{$localizeFilter('wallet_hint')}}</div>

      <Balance class="balance" :coinTicker="coinTicker" :data="coinWalletData"/>

      <div class="buttons-block" v-if="!isDemo">
        <div class="operation-buttons">
            <button class="transperent-button wallet-button" @click="showChooseNetworkModal=true">{{$localizeFilter('fill')}}</button>
            <button class="basic-button wallet-button" @click="showWalletWithdrawalModal=true">{{$localizeFilter('withdraw')}}</button>
        </div>
        <!-- <router-link
            :to="`/cabinet/withdrawalRequests?from=wallet&coin=${coinTicker}`">
        <button class="withdrawals-link">{{$localizeFilter('withdrawal_requests')}}</button>
        </router-link> -->
      </div>

      <!-- <CoinsPairsList :style="'margin-top:16px;'" :coinTicker="coinTicker"/> -->

      <Operations class="operations" :type="'wallet'" :showOptions="true" :coinTicker="coinTicker" :coinWalletData="coinWalletData"/>

      <ChooseNetworkModal v-if="showChooseNetworkModal" :coinTicker="coinTicker" @networkSelected="networkSelected" @closeModal="closeModals"/>
      <FillWalletModal v-if="showFillWalletModal" :coin="coinWalletData" @closeModal="closeModals" @changeNetwork="changeNetwork"/>
      <BuyCryptoModal v-if="showBuyCryptoModal" :buyCoin="buyCryptoData.buyCoin" :sellCoin="buyCryptoData.sellCoin" @cryptoBoughtSuccessful="cryptoBoughtSuccessful" @closeModal="closeModals"/>
      <WalletWithdrawalModal :coinTicker="coinTicker" v-if="showWalletWithdrawalModal" @closeModal="closeModals" @showConfirmationModal="showConfirmationModal" />
      <WalletWithdrawalConfirmationModal v-if="showWalletWithdrawalConfirmationModal" @closeModal="closeModals" :withdrawId="withdrawId" :commission="commission" :coinTicker="coinTicker" />
      <!-- <Modal v-if="showOperationStatusModal" :type="'operation'" :data="buyCryptoData" @closeModal="closeModals"/> -->
      <OperationStatusModal v-if="showOperationStatusModal" :data="buyCryptoData" @closeModal="closeModals"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import Operations from '@/components/Operations.vue'
import Balance from '@/components/Balance'
// import CoinsPairsList from '@/components/CoinsPairsList'
import ChooseNetworkModal from "@/components/newModals/ChooseNetworkModal.vue"
import FillWalletModal from "@/components/newModals/FillWalletModal.vue"
import WalletWithdrawalModal from "@/components/newModals/WalletWithdrawalModal.vue"
import WalletWithdrawalConfirmationModal from "@/components/newModals/WalletWithdrawalConfirmationModal.vue"
import BuyCryptoModal from "@/components/newModals/BuyCryptoModal.vue"
// import Modal from "@/components/modals/Modal.vue"
import OperationStatusModal from '../components/newModals/OperationStatusModal.vue'


export default defineComponent({
    props: {
        coinTicker: {
            type: String,
            required: true
        },
        pageOverride: {
            type: Number,
            default: 1
        },
        isDemoOverride: {
            type: Boolean,
            default: false
        }
    },
    components: {
    Operations,
    Balance,
    WalletWithdrawalModal,
    BuyCryptoModal,
    ChooseNetworkModal,
    // Modal,
    FillWalletModal,
    WalletWithdrawalConfirmationModal,
    OperationStatusModal
},
    data: ()=>({
      showChooseNetworkModal: false,
      showBuyCryptoModal: false,
      showWalletWithdrawalModal: false,
      showWalletWithdrawalConfirmationModal: false,
      buyCryptoData: null,
      showOperationStatusModal: false,
      showFillWalletModal: false,

      withdrawId: null,
      commission: 0n,
    }),
    computed: {
        coinWalletData() {
          return this.$store.getters.walletCoinByTicker(this.coinTicker) || {}
        },
        localizedCoinName() {
            return localizeFilter(`coin_${this.coinTicker}`)
        },
        selectedFillTypeCoin() {
          return this.$store.getters.coinByTicker(this.selectedFillTypeCoinTicker)
        },
        isDemo() {
            return this.$store.getters.isDemo
        },
    },
    setup() {

    },
    methods: {
      // selectFillType(buyCryptoData) {
        // this.closeModals()
        // setTimeout(()=>{
        //   this.buyCryptoData = buyCryptoData
        //   this.showBuyCryptoModal = true
        // },0)
      // },
      showConfirmationModal({withdrawId, commission}) {
        this.closeModals()
        console.log(withdrawId, commission)
        this.withdrawId = withdrawId
        this.commission = commission
        setTimeout(()=>{
          this.showWalletWithdrawalConfirmationModal = true
        },0)
      },
      changeNetwork() {
        this.closeModals()
        setTimeout(()=>{
          this.showChooseNetworkModal = true
        },0)
      },
      async networkSelected(network) {
        try {
          const data = {coinTicker: this.coinTicker, network}
          await this.$store.dispatch('getFillWalletAdddress', data)
          this.closeModals()
          setTimeout(()=>{
            this.showFillWalletModal = true
          },0)
        } catch(e) {
          this.$error(e)
        }
      },
      closeModals() {
          this.showChooseNetworkModal = false
          this.showFillWalletModal = false
          this.showBuyCryptoModal = false
          this.showWalletWithdrawalModal = false
          this.showWalletWithdrawalConfirmationModal = false
          this.buyCryptoData = null
          this.showOperationStatusModal = false
      },
      cryptoBoughtSuccessful(data) {
        // // console.log(data)
        this.closeModals()
        setTimeout(()=>{
          this.showOperationStatusModal = true
          // this.buyCryptoData = null
        },0)
      },
      async loadData() {
          try {
            // await this.$store.dispatch('getWalletCoin', {coinTicker: this.coinTicker})
            await this.$store.dispatch('getWalletData')
          }
          catch (e) {
              console.log(e)
              this.$error(e)
          }
      }
    },
    async mounted() {
        await this.$store.dispatch('setDemo', {isDemo: this.isDemoOverride})
        await this.loadData()
    }
})
</script>


